                  <p class="font-handel text-multi">
                    Chào các bạn!
                    </p>
                    <p class="font-handel text-multi"> &emsp; Tôi là Tuấn, đã tốt nghiệp Cử nhân Công nghệ thông tin tại Học viện Quản lý Giáo dục được gần 2 năm. Hiện tại đang là lập trình viên .Net Full-Stack tại một công ty về công nghệ ở Hà Nội. Công việc hằng ngày của tôi là gì thì chắc các bạn cũng biết rồi đấy (thường thì 40% thời gian xem youtube, 10% codding và tôi dành phần nhỏ thời gian còn lại để fix bug).
                    
                    </p>
                    <p class="font-handel text-multi">Đây là lần đầu tiên tôi viết blog. Do khả năng viết lách của tôi khá tệ nên tôi cần cải thiện hơn một chút, cũng hy vọng các kinh nghiệm bản thân có được trong quá trình học tập và làm việc có thể giúp ích cho các bạn có ý định theo nghề lập trình như tôi.
                  </p>
                  <h4 class="title-blog padding-title">Chặng đường trở thành lập trình viên của tôi</h4>
                  <p class="font-handel text-multi">
                    Có lẽ bắt đầu từ khi còn bé. Năm lớp 8 tôi bắt đầu được tạo cảm hứng bởi Thomas Romain và Tania Palumbo cùng tạo hình nhân vật Jeremie Belpois trong phim “Code Lyoko”. Có lẽ các bạn thế hệ của tôi không còn xa lạ gì bộ phim này đâu nhỉ (Thật ra cho đến gần cuối năm trung học tôi còn chưa biết tên 2 tác giả của bộ phim đâu @@). Nội dung chính của bộ phim nói về một nhóm thiếu niên cùng đi vào thế giới ảo có tên Lyoko để chống lại thế lực X.A.N.A có mục đích đe dọa đến trái đất. Nghe thôi đã thấy hào hứng rồi, cùng với sự ngầu lòi của các nhân vật, sáng tạo trong cách chiến đấu và sự thông minh của “Einstein” (đây là tên mà mọi người gọi Jeremie Belpois). Tôi chưa bỏ lỡ tập nào và cái hình dáng của mấy con boss, crep vẫn còn xuất hiện mỗi khi có ai đó nhắc đến lyoko hay điều gì tương tự. Nhưng như vậy vẫn chưa đủ vì đấy chứa 90% làm lý do để tôi có thể xem phim hoạt hình mà không cảm thấy tội lỗi. Đến bây giờ vẫn có người nói với tôi: “Thật tốt vì bạn có thể biết được mình mong muốn gì và điều bản thân mình thực sự thích từ sớm”. Đúng vậy, 10% còn lại của tôi là muốn được chơi game, thật là một điều mong muốn tuyệt vời nhỉ?
                  </p>
                  <img style="border-radius: 8px" width="100%" src="img/for_me/lyoko.jpg" />
                  <p class="font-handel text-multi">
                    <br /><br />Không như các anh chị đi trước, mọi người đều rất chăm chỉ tìm hiểu và tiếp cận lập trình từ rất sớm. Tôi thì khác, đến khi kết thúc THPT trong tôi chưa có khái niệm cụ thể nào về kiến thức CNTT. Tại thời điểm đó, tôi chưa bao giờ thắc mắc website được tạo ra như thế nào? Tại sao game lại vận hành được theo thao tác của người dùng? Tại sao khi có quá nhiều xử lý trong một thời điểm mà vẫn có thể vận hành mượt mà đến vậy?... Ok, khá lan man rồi, kết thúc THPT tôi quyết định thi quân sự (nhìn vậy chứ tôi đủ tiêu chuẩn thi quân đội đấy :)))) ), và trường tôi chọn là Sỹ quan Thông tin. Tôi trượt trong cả 2 năm thi, đến thời điểm đó tôi không còn biết thực sự mình nên học gì và làm gì sắp tới, tôi còn dự định sẽ không tiếp tục học nữa. Nhưng thật là duyên số và bổn phận không cho phép (bố mẹ tôi không có ý định đấy, vậy là bố tôi quyết định tạm thời sẽ cho đi học tại ĐH Vinh - chuyên ngành gì về kinh tế thì phải). Tôi vốn ghét mấy cái sự gò bó, nghe đến kinh tế đã thấy không có cảm hứng rồi. “Bố qua trường con học đi, CNTT điểm nguyện vọng 2 thấp thôi.” đấy là tin nhắn của “con gái” tôi (con gái tôi xinh lắm nhé! :))))). Vậy là tôi xách đít đi học và thành hậu bối của “con gái” mình trong khi bố tôi vẫn mong muốn tôi vào Vinh (do bạn ấy đi học từ năm đầu, còn tôi thi lại 1 năm). Duyên phận của tôi và học viện nơi tôi từng học là do "con gái" tôi tạo ra.
                    <br /><br />Lên đến đại học, vì học CNTT nên đương nhiên sẽ được mua máy tính từ rất sớm, tôi thấy mình thật sáng suốt (chơi game hết ngày này qua ngày khác). Tôi từng đạt được mức điểm trung bình rất ấn tượng trong suốt quãng đời học sinh - sinh viên ngay tại học kỳ đầu tiên (ấn tượng như nào thì tùy các bạn nghĩ nhé!). Lên đến năm 2, sau khi thoát khỏi những kiến thức bổ ích về duy vật và duy tâm, tôi bắt đầu được học chuyên ngành, môn đầu tiên là C. Đến tận khi đó tôi mới biết việc hiển thị được dòng chữ “Hello world!” lên màn hình đúng là một kỳ tích. Tôi bắt đầu quay lại việc học tập, điều đầu tiên tôi làm là coppy code C về ma trận trên các diễn đàn về xong chạy trên máy mình, đến cuối kỳ ngồi đọc thuộc và đi thi. Èo, tưởng thế nào chứ học lập trình dễ thật (tôi từng có cái suy nghĩ đần độn đó đấy). Một phần cảm hứng học tập của tôi được lấy lại vào kỳ học tiếp theo. Sang kỳ tới, tôi được trải nghiệm một cách lập trình hoàn toàn mới trên C++ mà giờ tôi vẫn gọi là cách lập trình quốc dân, đấy là OOP – lập trình hướng đối tượng. Thật may mắn khi cô giáo dạy chúng tôi môn này rất tận tình, cũng là lần đầu tiên tôi được làm một bài tập lớn và thuyết trình thay vì việc thi cử nhàm chán. Nhận đề tài xong, tôi miệt mài tìm hiểu mọi thứ liên quan và cố gắng mở rộng nó tối đa bằng kiến thức mình có, cùng với việc tôi hỏi cô rất nhiều về những thắc mắc mà bản thân không thể giải quyết, lúc thì cô giải đáp, lúc thì cô gửi link cho tôi tự đọc và làm theo. Sau kỳ đấy, môn của cô tôi được 10 điểm cuối kỳ, và đương nhiên là điểm A+ đầu tiên trong lịch sử thời sinh viên.
                    <br /><br /> Nháy mắt đến năm 3, tôi cũng đã có một chút biết về lập trình nên đã chọn chuyên ngành về lập trình nâng cao thay vì những mảng khác. Khi đó tôi có niềm đam mê bất tử về các thuật toán, đặc biệt là mã hóa và ký số (cho đến hiện tại vẫn vậy). Tôi quyết định bỏ tất cả thời gian rảnh để tìm hiểu và thực hành các thuật toán trong phạm vi tôi nắm bắt được. Nó thực sự có ích, vô tình điều đó khiến cho việc xử lý các iss của tôi được nhanh nhạy hơn rất nhiều, tư duy về lập trình cũng tốt hơn đáng kể (có thể do tôi có sự hình dung khá tốt, vì thời THPT về hình không gian tôi cực tốt luôn – cái này khoe thôi chứ k liên quan gì). Tại thời điểm này thì phần cảm hứng còn lại về học tập của tôi được lấy lại (là gì thì blog sau tôi sẽ nói nhé! kk). Trong năm 3 tôi bắt đầu học về C# và hệ sinh thái về lập trình của Microsoft, tôi định hướng được là mình thật sự thích hợp với nó. Kết thúc năm 3, số lượng A+ của tôi khá nhiều và nó đủ sức kéo kỳ tích năm nhất lên mức trên khá, mốc 2.5 tôi từng nghĩ bản thân sẽ khó đạt được ấy đã thành hiện thực. Lên năm 4, mọi thứ tại trường đối với tôi đều thấy không quá khó khăn, kỳ cuối cùng tôi quyết định đi làm. Tôi chọn FPT Software làm điểm tựa đầu tiên và tôi cũng đã đạt được điều đó. Mọi sự bận rộn của tôi trở lại do tôi phải đến cty full-time đồng thời làm đồ án tốt nghiệp tại trường. Sự bận rộn đấy cũng là điểm đánh dấu tôi chính thức trở thành một lập trình viên.
                    <br/><br />Một câu nói của Michael Jordan: “I can accept failure, everyone fails at something. But I can’t accept not trying.”
                    <br/><br />Phần tới tôi sẽ nói về cách tự học mà tôi thấy hiệu quả. Cảm ơn các bạn vì đã đọc!
                  </p>
