import Vue from "vue";
import Router from "vue-router";
import AppHeader from "./layout/AppHeader";
import AppFooter from "./layout/AppFooter";
import Components from "./views/Components.vue";
import Landing from "./views/Landing.vue";
import Login from "./views/Login.vue";
import Register from "./views/Register.vue";
import Profile from "./views/Profile.vue";
import BlogContent from "./views/ChangDuongTroThanhLaptrinhVienCuaToi.vue"; 

Vue.use(Router);

export default new Router({
  // linkExactActiveClass: "active",
  mode: 'history',
  routes: [
    {
      path: "/",
      name: "components",
      components: {
        header: AppHeader,
        default: Components //,
        //footer: AppFooter
      }
    },
    {
      path: "/profile",
      name: "components",
      components: {
        header: AppHeader,
        default: Components //,
        //footer: AppFooter
      }
    },
    {
      path: "/profile/blog",
      name: "blog",
      components: {
        header: AppHeader,
        default: Landing,
        footer: AppFooter
      }
    },
    {
      path: "/blog",
      name: "blog",
      components: {
        header: AppHeader,
        default: Landing,
        footer: AppFooter
      }
    },
    {
      path: "/login",
      name: "login",
      components: {
        header: AppHeader,
        default: Login,
        footer: AppFooter
      }
    },
    {
      path: "/register",
      name: "register",
      components: {
        header: AppHeader,
        default: Register,
        footer: AppFooter
      }
    },
    {
      path: "/profile/profile",
      name: "profile",
      components: {
        header: AppHeader,
        default: Profile,
        footer: AppFooter
      }
    },
    {
      path: "/profile/blog/chang_duong_tro_thanh_lap_trinh_vien_cua_toi",
      name: "ChangDuongTroThanhLaptrinhVienCuaToi",
      components: {
        header: AppHeader,
        default: BlogContent,
        footer: AppFooter
      }
    },
    {
      path: "/blog/chang_duong_tro_thanh_lap_trinh_vien_cua_toi",
      name: "ChangDuongTroThanhLaptrinhVienCuaToi",
      components: {
        header: AppHeader,
        default: BlogContent,
        footer: AppFooter
      }
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
